import React, { Component } from "react";
import CustormNavbar from "../navbar/CustormNavbar";
import "./Header.css";
import {
  Form,
  FormControl,
  Button,
  Jumbotron,
  Container,
  Breadcrumb,
} from "react-bootstrap";

class Header extends Component {
  render() {
    return (
      <div className="header-container">
        <Breadcrumb className="float-right">
          <Breadcrumb.Item href="#">ช่วยเหลือ</Breadcrumb.Item>
          <Breadcrumb.Item href="https://getbootstrap.com/docs/4.0/components/breadcrumb/">
            สมัครสมาชิก
          </Breadcrumb.Item>
          <Breadcrumb.Item active>เข้าสู่ระบบ</Breadcrumb.Item>
        </Breadcrumb>
        <Jumbotron fluid>
          <Container>
            <h3>E-commerce</h3>
            <Form inline>
                <FormControl type="text" placeholder="ค้นหาสินค้าและร้านค้า" className="search-bar"/>
                <i class="fas fa-search search-btn"></i>
            </Form>
          </Container>
        </Jumbotron>
      </div>
    );
  }
}

export default Header;
